.filter {
    &__reset {
        cursor: pointer;
        margin: 0 5px 0 5px;
        &:hover {
            color: #D2B48C;
            transition: 0.3s;
        }
    }
    &__click {
        display: flex;
        align-items: center;
        &-text {
            margin-right: 15px;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: #000000;
        }
        &-button {
            cursor: pointer;
            margin: 0 5px 0 5px;
            width: 75px;
            height: 30px;
            background: #FFFFFF;
            box-shadow: 4px 4px 20px rgb(0 0 0 / 25%);
            border-radius: 4px 0px 0px 4px;
            font-weight: 400;
            font-size: 12px;
            line-height: 17px;
            text-align: center;
            color: #000000;
            border: none;
            &:hover {
                color: #FFFFFF;
                background: #D2B48C;
                transition: 0.5s;
            }
            &:active {
                color: #000000;
                background: #FFFFFF;
                transition: 0.1s;
            }
        }
    }
}

.active {
    color: #FFFFFF;
    background: #D2B48C; 
}